* {
  font-family: "Oswald", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

$yellow: #ffe600;
$white: #fff;
$black: #131316;
$grey: #515151;
$lightgrey: #707070;

a {
  text-decoration: none;
}

li {
  list-style-type: square;
  color: $yellow;
}

img {
  display: block;
  max-width: 100%;
}

button:active,
button:focus {
  outline: none;
}

section + section {
  padding-top: 60px;
}

.container {
  max-width: 1200px;
  margin: auto;
  padding: 0 15px;
}

.home_bg {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: url(../img/bg/home.jpg) fixed;
  background-size: cover;
  position: relative;
}

.header {
  position: fixed;
  background-color: rgba(19, 19, 22, 0.7);
  width: 100vw;
  z-index: 10;
}

.header_logo img {
  height: 50px;
}

.header_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 0;
}

.menu {
  display: flex;
}

.menu li {
  margin-right: 50px;
}

.menu li:last-child {
  margin-right: 0;
}

.menu_link {
  color: $white;
  text-transform: uppercase;
  transition: 0.5s all;
}

.menu_link:hover {
  color: $yellow;
  transition: 0.5s all;
}

#mobile_btn {
  display: none;
}

.row_bg {
  max-width: 100vw;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  margin: auto 0;
}

.row_bg_inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 30px;
}

.row_bg_title {
  font-size: 60px;
  font-weight: bold;
  color: $white;
  padding: 0 10px;
  margin-bottom: 10px;
}

.button {
  display: block;
  max-width: 160px;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  background-color: $yellow;
  color: $black;
  border-radius: 2px;
  padding: 10px;
  transition: 0.1s all;
  margin: 0 auto;
}

.button:hover {
  background-color: $white;
  color: $yellow;
  transition: 0.1s all;
}

.arrow_top {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.2s;
}

.arrow_link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  background-color: $yellow;
  margin: 0 auto;
}

.arrow_link img {
  transition: 0.2s;
}

.arrow_link:hover img {
  transform: scale(1.5);
  transition: 0.2s;
}

.section_info {
  text-align: center;
  margin-bottom: 60px;
}

.section_title {
  font-size: 16px;
  font-weight: 400;
  color: $grey;
  text-transform: uppercase;
}

.section_name {
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 700;
  padding: 20px 0;
  color: $black;
}

.section_name-line {
  border: 1px solid $yellow;
  max-width: 170px;
  margin: auto;
}

.section_text {
  font-family: sans-serif;
  color: $lightgrey;
  padding: 10px;
}

.section_items {
  display: flex;
  justify-content: space-between;
  text-align: center;
  padding-bottom: 60px;
}
.items_block {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25%;
  margin: 0 10px;
}

.block_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  margin-bottom: 20px;
}

.bg_icon {
  background-color: $yellow;
  border-radius: 50%;
}

.block_title {
  text-transform: uppercase;
  padding: 0 10px;
  margin-bottom: 10px;
}

.block_text {
  font-family: sans-serif;
  color: $grey;
  padding: 10px;
}

.service_bg {
  background: url(../img/bg/service.jpg) fixed;
  background-size: cover;
}

.wrap_bg {
  background-color: rgba(0, 0, 0, 0.9);
}

.wrap_bg .section_name {
  color: $white;
}

.wrap_bg .section_text {
  color: $white;
}

.works {
  color: $white;
  width: 16.666%;
}

.works .block_text {
  color: $white;
}

.works_list {
  margin: 60px 0;
}

.works_list ul {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.works_list li {
  text-transform: uppercase;
  padding: 10px;
  margin: 0 15px;
}

.works_list li a {
  color: $grey;
}

.works_desc {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
}

.desc_img {
  flex: 1;
}

.desc_info {
  flex: 1;
  margin-left: 25px;
}

.desc_title {
  font-size: 25px;
}

.desc_text {
  color: $lightgrey;
  font-family: sans-serif;
  margin-top: 20px;
}

.works_img {
  max-width: 100vw;
  margin-bottom: 50px;
}

.works_row {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  cursor: pointer;
}

.works_row img {
  max-width: 16.666%;
}

.works_btn:hover {
  background-color: $black;
}

.works_bg {
  background: url(../img/bg/works.jpg) fixed;
  background-size: cover;
}

.numbers {
  justify-content: center;
  width: 25%;
  height: 150px;
  border: 1px solid $yellow;
}

.numbers_title {
  font-size: 40px;
  color: $white;
  font-weight: 700;
}

.numbers_text {
  color: $yellow;
}

.about_block_wrapper {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 60px;
}

.about_block {
  display: flex;
  width: 50%;
  padding: 10px;
}

.about_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 50px;
  height: 50px;
  background: $yellow;
  border-radius: 50%;
  margin-right: 10px;
}

.about_desc {
  text-align: left;
}

.team_section-bg {
  background-color: rgba(70, 70, 70, 0.1);
}

.team_box {
  display: flex;
  flex-direction: column;
  width: 25%;
  background: $white;
  cursor: pointer;
  border-bottom: 2px solid $black;
  transition: border-bottom 0.2s ease-out;
  margin: 0 10px;
}

.team_box:hover {
  border-bottom: 2px solid $yellow;
  transition: border-bottom 0.2s ease-out;
}

.team_box_img {
  height: 270px;
}

.team_box_img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.team_box_title {
  font-size: 20px;
  padding: 10px;
  color: $yellow;
  text-transform: uppercase;
}

.team_box_text {
  font-size: 20px;
  color: $black;
  padding: 10px;
  text-transform: uppercase;
  margin-top: auto;
}

.team_input {
  display: none;
}

.team_wrap_bg {
  display: none;
  background: $black;
}

.team_input:checked + .team_wrap_bg {
  display: block;
}

.team_desc {
  max-width: 820px;
  padding: 40px 0;
  margin: auto;
}

.team_desc_text {
  color: $white;
  padding: 10px;
}

.team_social {
  display: inline-flex;
  padding: 10px;
}

.social_icons {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border: 1px solid $white;
  margin-left: 30px;
}

.social_icons:first-child {
  margin-left: 0;
}

.social_icons:hover {
  background-color: $yellow;
}

.team-section_bg {
  background: url(../img/bg/about.jpg) fixed;
  background-size: cover;
}

.block_circle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 140px;
  height: 140px;
  border: 1px solid $yellow;
  border-radius: 50%;
}

.circle_title {
  font-size: 35px;
  color: $yellow;
}

.circle_text {
  text-transform: uppercase;
  font-size: 20px;
  color: $yellow;
  margin-top: 20px;
}

.slider {
  padding: 0 30px 50px;
}

.slick-list {
  overflow: hidden;
}

.slick-track {
  display: flex;
}

.slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
}

.slick-dots li {
  list-style: none;
  margin: 0 10px;
}

.slick-dots button {
  font-size: 0;
  width: 10px;
  height: 10px;
  background-color: $yellow;
  border-radius: 50%;
}

.slick-active button {
  background-color: $lightgrey;
}

.slider_item img {
  margin: 0 auto;
}

.clients-section_bg {
  background: url(../img/bg/clients.jpg) fixed;
  background-size: cover;
}

.clients-section_bg .section_info {
  margin-bottom: 0;
}

blockquote {
  display: inline-block;
  max-width: 600px;
  padding: 40px 0;
}

.our_mail {
  color: $lightgrey;
}

.contacts_section_bg {
  background: url(../img/bg/contacts.jpg) fixed;
  background-size: cover;
}

form {
  text-align: center;
  padding-bottom: 20px;
}

.contact_form {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.contact_form input {
  width: 365px;
  background: none;
  padding: 20px;
  margin-left: 20px;
  color: $white;
}

.contact_form input:first-child {
  margin-left: 0;
}

input:focus {
  outline: 2px solid $yellow;
  background-color: rgba(19, 19, 22, 0.5);
  box-shadow: $white;
}

textarea {
  display: block;
  width: 100%;
  max-height: 220px;
  color: $white;
  background: none;
  padding: 20px;
  margin-bottom: 20px;
}

textarea:focus {
  outline: 2px solid $yellow;
  background-color: rgba(19, 19, 22, 0.5);
  resize: none;
}

button {
  border: none;
  cursor: pointer;
}

form button:focus {
  outline: 3px solid $yellow;
  background-color: rgb(41, 58, 211);
}

footer {
  background-color: $black;
}

.copyright {
  text-align: center;
  color: $white;
  padding: 30px 0;
}

.copyright a {
  color: $yellow;
}

.footer_social {
  display: flex;
  justify-content: center;
  padding-bottom: 25px;
}

.footer_social .social_icons {
  position: relative;
  border: none;
  background-color: transparent;
}

.hover_icons {
  display: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: 0.5s all;
}

.social_icons:hover .hover_icons {
  display: block;
  transition: 0.5s all;
}

@media (max-width: 1170px) {
  .works_row img {
    max-width: 25%;
  }
}

@media (max-width: 991px) {
  .works_row img {
    max-width: 33.333%;
  }
}

@media (max-width: 767px) {
  .menu_btn {
    width: 30px;
    height: 20px;
    position: relative;
  }

  .menu_btn span {
    width: 100%;
    height: 2px;
    background-color: $white;
    position: absolute;
    top: 9px;
    transition: 0.2s;
  }

  .menu_btn::before,
  .menu_btn::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: $white;
    position: absolute;
    transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
  }

  .menu_btn::before {
    top: 0;
  }

  .menu_btn::after {
    bottom: 0;
  }

  .menu {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    background-color: rgba(19, 19, 22, 0.7);
    text-align: center;
    width: 100vw;
    margin-top: 17px;
    max-height: 0;
    overflow: hidden;
    transition: 0.5s all;
  }

  .menu li {
    margin-right: 0;
  }

  .menu .menu_link {
    display: block;
    padding: 20px;
    border-bottom: 1px solid $yellow;
  }

  #mobile_btn:checked ~ .menu {
    max-height: 500px;
    transition: 0.5s all;
  }

  #mobile_btn:checked ~ .menu_btn span {
    height: 0;
    transition: 0.2s;
  }

  #mobile_btn:checked ~ .menu_btn::before {
    transform: rotate(45deg);
    top: 50%;
    transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  }

  #mobile_btn:checked ~ .menu_btn::after {
    transform: rotate(-45deg);
    top: 50%;
    transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  }

  .slider_title {
    font-size: 40px;
  }
  .menu_btn {
    display: block;
  }

  .section_items {
    flex-direction: column;
    align-items: center;
  }
  .items_block {
    width: 100%;
  }
  .about_block {
    width: 100%;
  }
  .team_box {
    align-items: center;
    width: 100%;
    padding-top: 10px;
  }
  .works_desc {
    flex-direction: column;
  }
  .desc_info {
    margin-left: 0;
  }
  .works_row img {
    max-width: 50%;
  }
  .team_box_img {
    display: inline-block;
    left: 0;
  }
  .team_box_img img {
    width: 320px;
  }
  .circle_wrapper {
    display: flex;
    flex-direction: column-reverse;
  }
  .block_icon {
    margin-bottom: 0;
  }
  .contact_form {
    flex-direction: column;
    margin-bottom: 0;
  }
  .contact_form input {
    width: 100%;
    margin: 0 0 15px 0;
  }
}

@media (max-width: 560px) {
  .works_row img {
    max-width: 100%;
  }
}
